<template>
  <form>
    <label>
      Nazwa [starts with letter! wal!]
      <input type="text" v-model="name" class="input">
    </label>
    <label>
      Liczba numerów
      <input type="number" v-model="number" class="input">
    </label>
    <label>
      Dzielnik
      <input type="number" v-model="divider" class="input">
    </label>
    <label>
      Data pobrania
      <input type="date" v-model="dateTaken" class="input">
    </label>
    <p v-if="validated" class="error">Wypełnij wszystkie pola</p>
    <button :disabled="validated" type="button" @click="save" class="btn">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="48px"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z"/></svg>
    </button>
    <button type="button" @click="cancel" class="btn">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="48px"><path opacity=".87" fill="none" d="M0 0h24v24H0V0z"/><path d="M12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8zm3.59-13L12 10.59 8.41 7 7 8.41 10.59 12 7 15.59 8.41 17 12 13.41 15.59 17 17 15.59 13.41 12 17 8.41z"/></svg>
    </button>
  </form>
</template>

<script>

export default {
  name: 'New',
  data() {
    return {
      name: '',
      number: '',
      divider: '',
      dateTaken: '',
    };
  },
  computed: {
    validated() {
      return this.name === '' || this.number === '' || this.divider === '' || this.dateTaken === '';
    },
  },
  methods: {
    save() {
      this.$store.dispatch('add', {
        name: this.name,
        number: this.number,
        divider: this.divider,
        dateTaken: this.dateTaken.toString(),
      });

      this.$router.go(-1);
    },
    cancel() {
      this.$router.go(-1);
    },
  },
  created() {
    this.dateTaken = new Date().toISOString().slice(0, 10);
  },
};
</script>

